.main_Popup {
  width: 100%;
  height: 100vh;
  background-color: rgb(20, 20, 20, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  position: absolute;
}
.Wrapper {
  background-color: white;
  width: 90%;
  height: 500px;
  padding-bottom: 15px;
  overflow: scroll;
}
.img-cont {
  margin: 10px;
  display: inline-block;
  cursor: pointer;
}
.img-cont img {
  height: 120px;
  width: 120px;
  margin: 5px;
}
.select {
  background-color: lightgray;
  width: 200px;
  height: 40px;
  color: black;
  margin-top: 30px;
  margin-left: 30px;
}
.input-box {
  background-color: lightgray;
  width: 200px;
  height: 38px;
  color: black;
  margin-right: 40px;
  margin-top: 30px;
}
.button {
  width: 200px;
  height: 40px;
  background-color: green;
  color: white;
  cursor: pointer;
  margin-top: 30px;
  margin-left: 350px;
}
.grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 70px;
}
